import { Modal, Table } from 'react-bootstrap';
import './Cart.css';
import { useDispatch, useSelector } from 'react-redux';
import { increase } from './../store.js';

function Cart() {

    let state = useSelector((state)=> state.cart);
    console.log(state)
    
    let dispatch = useDispatch();    
    return (
        <>     
            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Product</th>
                        <th>Count</th>
                        <th>NULL</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        state.map((a, i) => {
                            return (
                                <tr key={i}>
                                    <td>{state[i].id}</td>
                                    <td>{state[i].name}</td>
                                    <td>{state[i].count}</td>
                                    <td><button onClick={() => {
                                        dispatch(increase(state[i].id))
                                    }}>+</button></td>
                                </tr>
                            )
                        })
                    }

                </tbody>
            </Table>

        </>
    );
}

export default Cart;